	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Binary Security Updates for FreeBSD","date":"19 Sep 2003","slug":"/2003-09-19-binary-security-updates-for-freebsd/","tags":"Y2003"},"html":"<p><a title=\"a client-server binary diff solution\" href=\"http://www.daemonology.net/freebsd-update/\">Binary Security Updates for FreeBSD</a></p>"},"next":{"fields":{"title":"Come In and Find The Exit","date":"18 Sep 2003","slug":"/2003-09-18-come-in-and-find-the-exit/"}},"previous":{"fields":{"title":"Let's Don't be Beastly to the Germans","date":"19 Sep 2003","slug":"/2003-09-19-lets-dont-be-beastly-to-the-germans/"}}},{"node":{"fields":{"title":"Come In and Find The Exit","date":"18 Sep 2003","slug":"/2003-09-18-come-in-and-find-the-exit/","tags":"Y2003"},"html":"<p>The Abendblatt had an article today on how <a title=\"\"Komm rein und finde raus\"\" href=\"http://www.abendblatt.de/daten/2003/09/18/209322.html\">English ad slogans are not understood by German consumers. Of the 12 slogans they listed, only 2 were understood by more the half the readers, and the median was 25%. A number of people thought they understood the slogans, but actually did not (the Douglas slogan 'Come in and find out' was incorrectly thought to mean 'Come in and find the exit'). The worst undersood slogans were Mitsubishi's 'Drive Alive' ('Survive the trip'), Siemens' 'Be inspired', and RWE's 'One Group. Multi Utilities', to which even a native English speaker can only say 'Huh?'</p>"},"next":{"fields":{"title":"Bin Bisy Backson","date":"18 Sep 2003","slug":"/2003-09-18-bin-bisy-backson/"}},"previous":{"fields":{"title":"Binary Security Updates for FreeBSD","date":"19 Sep 2003","slug":"/2003-09-19-binary-security-updates-for-freebsd/"}}},{"node":{"fields":{"title":"Bin Bisy Backson","date":"18 Sep 2003","slug":"/2003-09-18-bin-bisy-backson/","tags":"Y2003"},"html":"<p>I haven't had much time for posting lately, if you haven't noticed. At work I'm managing a provider, domain and IP switch, which should culminate this weekend when we unplug the router from the old provider. We've also had two electric outages in the past two weeks, which despite the best preparations involve several man-hours of clean-up afterwards. At home I've on single coverage this week as FP runs a pep rally for her company in Bremen. And Christopher's grandmother arrives tomorrow for a 2-1/2 week visit. We've had some nice Indian Summer weather the past couple of days, let's hope it keeps up!</p>"},"next":{"fields":{"title":"Reversing Regular Expressions","date":"17 Sep 2003","slug":"/2003-09-17-reversing-regular-expressions/"}},"previous":{"fields":{"title":"Come In and Find The Exit","date":"18 Sep 2003","slug":"/2003-09-18-come-in-and-find-the-exit/"}}}],"pathPrefix":"","first":false,"last":false,"index":627,"pageCount":1101}}

